<template runat="server">
    <div id="thisPage">
        <div id="secondMenu">
            <!--<img id="back" class="back" src="../assets/imgs/back.png" alt="返回" />-->
            <div class="header1">
                <!--<p class="logo"><img src="../assets/imgs/dataman.png" alt="dataman"/></p>-->
                <div class="content">
                    <el-form>
                        <el-form-item id="dropMenu">
                            <el-select v-model="form.value" @change="areaChange">
                                <el-option label="图书" value="first"></el-option>
                                <el-option label="学术搜索" value="second"></el-option>
                                <el-option label="超星" value="third"></el-option>
                                <el-option label="校内网站" value="four"></el-option>
                                <el-option label="业务系统" value="five"></el-option>
                                <el-option label="同心云" value="six"></el-option>
                                <el-option label="百度" value="seven"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-form>
                    <div class="searchBox" v-if="baiduHide">
                        <el-form class="search " ref="form1" :model="form1" onsubmit="return false">
                            <!--<el-input placeholder="搜索" class="ipt" v-model="form1.name"></el-input>-->
                            <div class="ipt el-input">
                                <input type="text" placeholder="搜一下~" class="el-input__inner" v-model="form1.name"
                                       @keyup.13="search"/>
                            </div>
                            <el-button @click="search"></el-button>
                        </el-form>
                    </div>


                    <!--<div class="searchBox" v-if="baiduShow">-->
                        <!--<el-form class="search " ref="form1" :model="form1" onsubmit="return false">-->
                            <!--&lt;!&ndash;<el-input placeholder="搜索" class="ipt" v-model="form1.name"></el-input>&ndash;&gt;-->
                            <!--<div class="ipt el-input">-->
                                <!--<input type="text" placeholder="搜一下~" class="el-input__inner" v-model="form1.name"-->
                                       <!--@keyup="get($event)" @keydown.down="next" @keydown.up.prevent="last"/>-->
                            <!--</div>-->
                            <!--<el-button @click="search"></el-button>-->
                        <!--</el-form>-->
                        <!--<ul class="tip">-->
                            <!--<li @click="searchFor($event)" v-for="(item,index) in searchList">{{item}}</li>-->
                        <!--</ul>-->
                    <!--</div>-->
                    <div class="iframeThis">
                        <Library v-if="library"></Library>
                        <Superstar v-if="superstar"></Superstar>
                        <System v-if="system"></System>
                        <Web v-if="web"></Web>
                        <Tongxinyun v-if="tongxinyun"></Tongxinyun>
                        <Baidu v-if="baidu"></Baidu>
                        <Scholar v-if="scholar"></Scholar>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/javascript" src="../assets/js/rem.js "></script>
<script type="text/javascript" src="../assets/js/jquery-1.12.4.min.js"></script>
<script>
    document.documentElement.style.fontSize = document.documentElement.clientWidth / 10 + 'px';
    import System from './system.vue'
    import Web from './web.vue'
    import Tongxinyun from './iframe.vue'
    import Library from './library.vue'
    import Baidu from './baidu.vue'
    import Scholar from './scholar.vue'
    import Superstar from './superstar.vue'
    import {commonData} from '../assets/js/commonData.js'

    export default {
        data() {
            return {
                searchList: [],//搜索弹出列表
                t1: '',//输入框里面的值
                now: -1,
                form1: {
                    name: ''
                },
                form: {
                    value: 'first',
                },
                scholarRoute: Scholar,
                scholar: false,
                system: false,
                web: false,
                tongxinyun: false,
                baidu: false,
                superstar: false,
                library: false,
                baiduHide: true,
                baiduShow: false
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                this.tableView();
            })
        },
        components: {System, Web, Tongxinyun, Library, Baidu, Scholar, Superstar},
        methods: {
            tableView: function () {
                if (commonData.activeName != '') {
                    this.form.value = commonData.activeName;
                    this.form1.name = commonData.searchContent;
                    this.searchContent();
                }

            },
            areaChange: function (value) {
//                if (this.form1.name != '') {
                    commonData.searchContent = this.form1.name;
                    this.searchContent();
//                }
            },
            search: function () {
                if (this.form1.name == '') {
                    this.$message({
                        showClose: true,
                        message: '搜索内容为空！'
                    });
                } else {
                    commonData.searchContent = this.form1.name;
                    this.searchContent();
                }
            },
            searchContent: function () {
                this.library = false;
                this.scholar = false;
                this.web = false;
                this.system = false;
                this.superstar = false;
                this.tongxinyun = false;
                this.baidu = false;
                if (this.form.value == 'first') {
                    this.scholar = true;
                } else if (this.form.value == 'second') {
                    this.library = true;
                } else if (this.form.value == 'third') {
                    this.superstar = true;
                } else if (this.form.value == 'four') {
                    this.web = true;
                } else if (this.form.value == 'five') {
                    this.system = true;
                } else if (this.form.value == 'six') {
                    this.tongxinyun = true;
                } else if (this.form.value == 'seven') {
//                    this.baiduHide = false;
//                    this.baiduShow = true;
                    this.baidu = true;
                }

            },
            get: function (evt) {
                if (evt.keyCode == 38 || evt.keyCode == 40) return;
                if (evt.keyCode == 13) {
                    window.open('https://wwww.baidu.com/s?wd=' + this.form1.name)
                    this.form1.name = ''
                }
                this.$http.jsonp("https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su", {//跨域请求数据
                    params: {
                        wd: this.form1.name//输入的关键词
                    },
                    jsonp: 'cb'//这里是callback
                }).then((res) => {//请求成功回调，请求来的数据赋给searchList数组
                    this.searchList = res.body.s
                }, (res) => {//失败显示状态码
                    alert(res.status)
                })
            },
            next: function () {//向下选择
                this.now++
                if (this.now == this.searchList.length) {//判断是否超出
                    this.now = 0
                }
                this.form1.name = this.searchList[this.now]
            },
            last: function () {//向上选择
                this.now--
                if (this.now < 0) {
                    this.now = this.searchList.length - 1
                }
                this.form1.name = this.searchList[this.now]
            },
            searchFor: function (e) {//鼠标点击跳转
                e.preventDefault();
//                console.log(index);
//                this.form1.name = this.searchList[index];
//                console.log(this.searchList[index]);
//                console.log(this.searchList);
//                this.now = index;
////                window.open('https://www.baidu.com/s?wd=' + this.form1.name);
//                this.baidu = true;
//                this.form1.name = '';
            }

        }
    }
</script>

<style>
    #dropMenu {
        position: relative;
        float: left;
        width: 30%;
    }

    /*#dropMenu  .el-input__inner{*/
    /*color:#999;*/
    /*}*/
    .el-select-dropdown {
        width: 31%;
        left: 2%;
    }

    .el-input__icon + .el-input__inner {
        padding-right: 5px !important;
    }

    .el-input__inner {
        padding: 3px 8px;
    }

