<template>

    <div id="third1" class="all">
        <div class="thirdAllContent">
            <div class="thirdAll" v-for="data in this.datas">
                <h3 v-if="data.TABLE_CNAME==='0'?false:true" v-html="data.TABLE_CNAME"></h3>
                <p v-if="data.TABLE_DESC==='0'?false:true" class="desc" v-html="data.TABLE_DESC"></p>
            </div>
        </div>
        <div class="errorInfo" v-if="errorInfo">
            <el-alert
                    title="错误提示"
                    type="error"
                    description="sorry，您访问的页面已走失"
                    show-icon>
            </el-alert>
        </div>
    </div>
</template>

<script>
    import {commonData} from '../assets/js/commonData.js'
    export default {

        data() {
            return {
                errorInfo:false,
                showAndHide: true,
                activeName: 'second',
                datas: [{}]
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                this.tableView()
            })
        },
        methods: {
            tableView: function () {
                var val = commonData.searchContent;
//                this.datas = commonData.system(val);
                var result = commonData.system(val);
                if (result instanceof Array) {
                    this.datas = result;
                } else {
                    this.errorInfo = true;
//                    $('.desc').css('border-bottom','none');
                }
            }
        }
    }
</script>

<style>

</style>
export { System };


