<template>
    <div id="third1" class="all">
        <div class="thirdAllContent">
            <div class="thirdAll" id="webAll" v-for="data in this.datas">
                <h3 v-html="data.title" v-if="data.title==='0'?false:true">{{data.title}}</h3>
                <p class="desc" v-html="data.content" v-if="data.content==='0'?false:true">{{data.content}}</p>
            </div>
        </div>
        <div class="errorInfo" v-if="errorInfo">
            <el-alert
                    title="错误提示"
                    type="error"
                    description="sorry，您访问的页面已走失"
                    show-icon>
            </el-alert>
        </div>

    </div>
</template>
<script>
    import {commonData} from '../assets/js/commonData.js'
    export default {
        data() {
            return {
                errorInfo:false,
                datas: [{}],
                showAndHide: true,
                activeName: 'second',
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                this.tableView()
            })
        },
        methods: {
            tableView: function () {
                var val = commonData.searchContent;
//                this.datas = commonData.show(val);
                var result = commonData.show(val);
                if (result instanceof Array) {
                    this.datas = result;
                } else {
                    this.errorInfo = true;
                    $('.desc').css('border-bottom','none');
                }
            }
        }
    }
</script>

<style>
    .packSearch {
        width: 96%;
        height: 54px;
        background: #EAF0F5;
        position: fixed;
        z-index: 100;
    }
    .desc{
        padding-bottom: 10px;
        border-bottom: 1px solid #EAF0F5;
    }

    .thirdAllContent {
        clear: both;
    }

    .packSearchwo {
        width: 96%;
        height: 54px;
        background: #EAF0F5;
        position: fixed;
        z-index: 100;
    }
    .el-alert__content i{
        display: none;
    }
    .el-alert__description{
        color: white!important;
    }
    .errorInfo{
        margin-top: -34px;
    }
</style>


