<template>

    <div id="third1">
        <div class="thirdAllContent1">
            <div class="thirdAll" v-for="(data,key,index) in this.datas">
                <div class="third-header">
                    <p class="signHead">
                        <img :src="data.userImg" alt=""/>
                    </p>
                    <div class="signName">
                        <p v-html="data.name">{{data.name}}</p>
                        <small>{{data.time}} &nbsp;{{data.day}}</small>
                    </div>
                </div>
                <div class="third-body">
                    <p id="descDetail" v-html="data.content">{{data.content}}</p>
                    <img :src="data.contentImg" v-if="!data.contentImg?false:true"/>
                </div>
                <div class="third-footer">
                    <a>转发<span v-html="data.forward" v-if="data.forward==='0'?false:true">( {{data.forward}} )</span></a>
                    <a>评论<span v-html="data.comment" v-if="data.comment==='0'?false:true">( {{data.comment}} )</span></a>
                    <a>赞<span v-html="data.favorite" v-if="data.favorite==='0'?false:true">( {{data.favorite}} )</span></a>
                </div>
            </div>
            <div class="errorInfo1" v-if="errorInfo">
                <el-alert
                        title="错误提示"
                        type="error"
                        description="sorry，您访问的页面已走失"
                        show-icon>
                </el-alert>
            </div>
        </div>
    </div>
</template>

<script>
    import {commonData} from '../assets/js/commonData.js'

    export default {

        data() {
            return {
                errorInfo:false,
                forward: false,
                comment: true,
                zan: true,
                datas: [{
                    comment: '',
                    day: '',
                    favorite: '',
                    forward: '',
                    name: '',
                    time: '',
                    userImg: ''
                }],
                showData: [{}],
                showAndHide: true,
                activeName: 'second'
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                $("a").removeAttr('href');
                this.tableView()
            })
        },
        methods: {

            tableView: function () {
                var val = commonData.searchContent;
//                this.datas = commonData.txcloud(val);
                var result = commonData.txcloud(val);
                if (result instanceof Array) {
                    this.datas = result;
                    this.showDatas(this.datas);
                } else {
                    $('.third-body').css('border-bottom','none');
                    $('.third-footer').css('display','none');
                    this.errorInfo = true;
                }
            },
            showDatas(datas){
                for(var i=0;i<datas.length;i++){
                    this.showData.push(datas[i]);
                    console.log(this.showData);
                }
            }
        }
    }
</script>

<style>
    html {
        word-break: break-all;
    }

    .thirdAll {
        padding: 5px;
        margin-top: -4px;
    }

    .thirdAllContent1 {
        clear: both;
    }
    #descDetail{
        margin-left:20px;
        padding-top:5px;
    }
    .errorInfo1{
        top:128px;
        position: fixed;
        width:96%;
    }
</style>


