<template>
    <div id="app">
        <router-view></router-view>
    </div>
</template>
<script type="text/javascript" src="assets/js/jquery-1.12.4.min.js"></script>
<script>


    export default {
        data(){
            return {
                searchContent:''
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                $('a').removeAttr('href');
            })
        },
    }

</script>

<style>
    body {
        background: #EAF0F5;
        font-family: "微软雅黑";
        font-size: 15px;
        position: relative;
        z-index: -100;
    }

    html {
        overflow: auto !important;
    }

    html, body, div, dl, dt, dd, ul, span, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, button, textarea, p, blockquote, th, td, img {
        margin: 0;
        padding: 0;
    }

    ul, ol {
        color: #666;
        list-style: none;
    }

    a {
        text-decoration: none;
    }

    input {
        outline: none;
        border: none;
        box-shadow: none;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both;
    }

    input[type="button"], input[type="submit"], input[type="reset"] {
        -webkit-appearance: none;
    }

    html {
        width: 100%;
        height: 100%;
    }

    body {
        width: 100%;
        height: 100%;
    }

    #app {
        width: 100%;
    }

    textarea {
        -webkit-appearance: none;
    }
</style>



