<template>
    <div id="third3" class="superstar">
        <div class="thirdAll2" >
            <div class="searchShow1" v-if="!this.datasOne.title?false:true">
                <div class="show1Left">
                    <img v-if="this.datasOne.image" :src="this.datasOne.image" alt=""/>
                    <img v-else src="../assets/imgs/book.png" alt=""/>
                </div>
                <div class="show1Right">
                    <p v-if="!this.datasOne.title?false:true"><span
                            v-html="this.datasOne.title">{{this.datasOne.title}}</span> &nbsp;<a class="showDetails">
                        <small>详细信息</small>
                    </a></p>
                    <p v-if="!this.datasOne.language?false:true"><span class="exp">语言:</span><span
                            v-html="this.datasOne.language">{{this.datasOne.language}}</span></p>
                    <p v-if="!this.datasOne.ISSN?false:true"><span class="exp">ISSN:</span><span
                            v-html="this.datasOne.ISSN">{{this.datasOne.ISSN}}</span></p>
                    <p class="qikan"><span class="exp">本刊期次:</span><span v-html="this.datasOne.guidedata"
                                                                         v-if="!this.datasOne.guidedata?false:true"></span>
                    </p>

                </div>
            </div>
            <div class="searchShow2" v-for="data in datas" v-if="!data.title?false:true">
                <div class="show2Left">
                    <img v-if="data.image" :src="data.image" alt=""/>
                    <img v-else src="../assets/imgs/book.png" alt=""/>
                    <!--<button class="saveBook">保存题库</button>-->
                </div>
                <div class="show2Right">
                    <p><span v-html="data.type" v-if="!data.type?false:true">{{data.type}}</span> &nbsp;<span
                            v-html="data.title" v-if="!data.title?false:true">{{data.title}}</span></p>
                    <p><span v-html="data.author" v-if="!data.author?false:true">{{data.author}}</span></p>
                    <p><span v-html="data.from" v-if="!data.from?false:true">{{data.from}}</span></p>
                    <p><span v-html="data.ISBN" v-if="!data.ISBN?false:true">{{data.ISBN}}</span></p>
                    <!--<p><span v-html="data.type"  v-if="!data.type?false:true">{{data.type}}</span> </p>-->
                    <p><span v-html="data.keyword" v-if="!data.keyword?false:true">{{data.keyword}}</span></p>
                    <!--<p><span v-html="data.abstracts"  v-if="!data.abstracts?false:true">{{data.abstracts}}</span></p>-->
                    <!--<div class="operate">-->
                    <!--<el-button type="primary" icon="share"></el-button>-->
                    <!--<el-button type="primary" icon="share"></el-button>-->
                    <!--</div>-->
                </div>

            </div>
        </div>
        <div class="errorInfo1" v-if="errorInfo">
            <el-alert
                    title="错误提示"
                    type="error"
                    description="sorry，您访问的页面已走失"
                    show-icon>
            </el-alert>
        </div>
    </div>
</template>
<!--<script type="text/javascript" src="../assets/js/jquery-1.12.4.min.js"></script>-->
<script>
    import {commonData} from '../assets/js/commonData.js'
    export default {
        data() {
            return {
                errorInfo:false,
                datas:[{}],
                datasOne: [{}],
                showDiv: true,
                forward: true,
                comment: true,
                favorite: true,
                flag: 'true'
            }
        },
        mounted: function () {
            this.$nextTick(function () {
               this.tableView();
            })
        },
        methods: {
             tableView: function () {
                 var val = commonData.searchContent;
                 this.datas = commonData.superstar(val);

                 var result = commonData.superstar(val);
                 if (result instanceof Array) {
                     this.datas = result;
                     this.datasOne = this.datas[0];
                     this.datas.shift();
                 } else {
                     this.errorInfo = true;
                 }
             }
        }
    }
</script>

<style>
    .thirdAll2 {
        padding: 0 5px 5px;
        /*margin-top:50px;*/
        clear: both;
    }

    .searchShow1 {
        background: #eaf0f5;
        padding-top: 5px;
        margin-top: 5px;
        overflow: hidden;
        margin-bottom: 5px;
    }

    .packSearchwo .el-button {
        border: none;
        position: absolute;
        height: 36px;
        top: 50%;
        right: 7px;
        margin-top: -18px;
        background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAwCAYAAABXAvmHAAADDElEQ…ASx+JjcwAb9bKYmzuQhYo2a+QO2KiXxdzcgSxUtFnjxTvwB4zeYEDFwzcrAAAAAElFTkSuQmCC) no-repeat;
        background-size: 32px 32px;
        cursor: pointer;
    }
    .el-alert .el-alert__description{
        font-size: 15px!important;
    }
    #third3 {
        /*padding-bottom: 5px;*/
        position: absolute;
        height: 91.3%;
        width: 100%;
        background: white;
        overflow-y: auto;
    }

    .show1Left img, .show2Left img {
        /*position: absolute;*/
        margin-top: 5px;
        float: left;
        width: 50px;
        height: 70px;
    }

    .show1Left img {
        padding-left: 5px;
    }

    .searchShow2 {
        margin-top: 15px;
        border-bottom: 1px dotted #eaf0f5;
        overflow: hidden;
    }

    .operate {
        position: absolute;
        right: 10px;
        top: 20px;
    }

    .exp {
        display: inline-block;
        width: 70px;
    }

    .show1Left, .show2Left {
        /*position: relative;*/
        width: 15%;
        float: left;
    }

    .show1Right, .show2Right {
        width: 82%;
        float: right;
        /*position: relative;*/
        padding-bottom: 5px;
    }

    .show2Right {
        padding-right: 5px;
    }

    .saveBook {
        /* display: inline-block; */
        line-height: 1;
        white-space: nowrap;
        cursor: pointer;
        background: #fff;
        border: 1px solid #c4c4c4;
        color: #1f2d3d;
        margin: 0;
        font-size: 12px;
        padding: 15px 2px;
        border-radius: 4px;
        background: none !important;
        width: 100%;
        background-color: #20a0ff
    }

    .showDetails {
        text-decoration: underline;
    }

    .qikan a {
        text-decoration: underline;
        margin-left: 5px;
    }
    html{

    }
</style>


