                    <!--<el-input type="text" placeholder="搜索的乐趣~" class="ipt" name="keyword" v-model="form0.search" @keyup></el-input>-->
                    <div class="ipt el-input">
                        <input type="text" placeholder="发现搜索的乐趣~" class="el-input__inner" v-model="form1.search"
                               @keyup.13="search1"/>
                    </div>
                    <!--<el-input type="search" style="display: none;" placeholder="搜索的乐趣~" class="ipt" v-model="form0.search" @keyup.13="searchPress($event)"></el-input>-->
                    <el-button @click="search1"></el-button>
                    <!--<el-button class="btn1" type="primary" icon="search" @click="search">搜索</el-button>-->
                </el-form>
                <!--</div>-->
                <br><br>


                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="图书" name="first"></el-tab-pane>

                    <el-tab-pane label="|"></el-tab-pane>
                    <el-tab-pane label="学术搜索" name="second"></el-tab-pane>
                    <el-tab-pane label="|"></el-tab-pane>
                    <!--<el-tab-pane label="超星" name="nine"></el-tab-pane>-->
                    <el-tab-pane label="超星" name="third"></el-tab-pane>
                </el-tabs>
                <br>
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="校内网站" name="four"></el-tab-pane>

                    <!--<el-tab-pane label="/"></el-tab-pane>-->
                    <el-tab-pane label="|"></el-tab-pane>
                    <el-tab-pane label="业务系统" name="five"></el-tab-pane>
                    <el-tab-pane label="|"></el-tab-pane>
                    <el-tab-pane label="同心云" name="six"></el-tab-pane>
                </el-tabs>
                <br>
                <el-tabs v-model="activeName" @tab-click="handleClick" id="fontChange">
                    <!--<el-tab-pane label="/"></el-tab-pane>-->
                    <el-tab-pane label="百度" name="seven"></el-tab-pane>
                    <el-tab-pane label="|"></el-tab-pane>
                    <el-tab-pane label="微信" name="eight"></el-tab-pane>
                    <el-tab-pane label="|"></el-tab-pane>
                    <el-tab-pane label="知乎" name="nine"></el-tab-pane>
