<script>
    import {commonData} from '../assets/js/commonData.js'
    document.documentElement.style.fontSize = document.documentElement.clientWidth / 10 + 'px';
    export default {

        data() {
            return {
                transitionName: 'slide-left',
                fullscreenLoading: false,
                form1: {
                    search: ''
                },
                activeName: 'first',
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                this.tableView();
            })
        },
        methods: {
            tableView: function () {
            },
            four(val){
                commonData.searchContent = val;
                this.$router.push({path: '/secondMenu'})
            },
            search1() {
             if (this.activeName == "first" || this.activeName == "second" || this.activeName == "third" || this.activeName == "four" || this.activeName == "five" || this.activeName == "six" || this.activeName == "seven") {
                 let _this = this;
                 if (_this.form1.search == '') {
                     commonData.activeName = _this.activeName = "first";
                 } else {
                     commonData.activeName = _this.activeName;
                 }
                 let val = _this.form1.search;
                 _this.four(val);
             }else{
                 return false;
             }

            },
            handleClick(tab, event) {
                console.log(tab);
                if (this.form1.search == '') {
                    return false;
                } else if (this.activeName == "first" || this.activeName == "second" || this.activeName == "third" || this.activeName == "four" || this.activeName == "five" || this.activeName == "six" || this.activeName == "seven") {
                    commonData.activeName = this.activeName;
                    let val = this.form1.search;
                    this.four(val);
                }
            },
            handleRow(index) {
                alert(index);
            }
        }
    }
</script>

