import SecondMenu from '../components/secondMenu.vue'
import Main from '../components/main.vue'
import Iframe from '../components/iframe.vue'
import System from '../components/system.vue'
import Web from '../components/web.vue'
import Library from '../components/library.vue'
import Baidu from '../components/baidu.vue'
import Scholar from '../components/scholar.vue'
import Superstar from '../components/superstar.vue'

const routers = [{
		path: '/main',
		name: 'main',
		component: Main
	},
	{
		path: '/secondMenu',
		name: 'secondMenu',
		component: SecondMenu
	},
	{
		path: '/',
		component: Main
	},
	{
		path: '/iframe',
		component: Iframe
	},
	{
		path: '/system',
		component: System
	},
	{
		path: '/web',
		component: Web
	},
	{
		path: '/library',
		component: Library
	},
    {
        path: '/baidu',
        component: Baidu
    } ,
    {
        path: '/scholar',
        name: 'scholar',
        component: Scholar,
        meta: {
            keepAlive: false // 不需要被缓存
        }
    },
    {
        path: '/superstar',
        component: Superstar
    }
]
export default routers


