<template>
    <div id="third1">
        <div class="thirdAllContent2">
            <div class="thirdAll" v-for="(data,$index) in datas">
                <!--<div class="iframeBaidu">-->
                <div class="third-left">
                    <p class="signHead">
                        <img v-if="data.thumbnail_small" :src="data.thumbnail_small"/>
                        <img v-else src="../assets/imgs/book.png" alt=""/>
                    </p>
                </div>
                <div class="third-right">
                    <div class="third-top">
                        <p>
                            <a class="title" v-html="data.title"  v-if="!data.title?false:true">{{data.title}}</a><br/>
                            <span v-html="data.author"  v-if="!data.author?false:true">著者<a class="author">{{data.author}}</a></span>
                            <small><span v-html="data.publication_title" v-if="!data.publication_title?false:true">{{data.publication_title}}</span>
                                <span v-html="data.publication_date" v-if="!data.publication_date?false:true">{{data.publication_date}}</span> <span
                                        v-html="data.volumes" v-if="!data.volumes?false:true">&nbsp;卷{{data.volumes}}</span> <span
                                        v-html="data.issues" v-if="!data.issues?false:true">&nbsp;期{{data.issues}}</span></small>
                        </p>
                        <p class="qikan">期刊文章：<a v-if="!data.fulltext_link?false:true" class="article">在线全文</a></p>
                    </div>
                    <br>

                    <div class="third-foot">
                        <a class="moreInfo" @click="moreInfo($event)">更多信息</a>
                        <div class="showDiv">
                            <p v-html="data.abstract" v-if="!data.abstract?false:true">{{data.abstract}}</p>
                            <p v-if="!data.databases_name?false:true"><span class="exp">出版来源:</span><span v-html="data.databases_name"></span></p>
                            <p v-if="!data.pages?false:true"><span class="exp">页码:</span><span v-html="data.pages"></span></p>
                            <p v-if="!data.page_counts?false:true"><span class="exp">页数:</span><span v-html="data.page_counts"></span></p>
                            <p v-if="!data.issns?false:true"><span class="exp">ISSN:</span> <span v-html="data.issns"></span></p>
                            <p v-if="!data.eissns?false:true"><span class="exp">EISSN:</span> <span v-html="data.eissns"></span></p>
                            <p v-if="!data.dois?false:true"><span class="exp">DOI:</span> <span class="doi"><span v-html="data.dois"></span></span></p>
                            <p v-if="!data.genres?false:true"><span class="exp">类型:</span> <span v-html="data.genres"></span></p>
                            <p v-if="!data.copyrights?false:true"><span class="exp">版权:</span> <span v-html="data.copyrights"></span></p>
                            <p v-if="!data.subject_terms?false:true"><span class="exp">主题:</span> <span v-html="data.subject_terms"></span></p>
                            <p v-if="!data.languages?false:true"><span class="exp">语言:</span> <span v-html="data.languages"></span></p>
                            <p v-if="!data.lc_call_numbers?false:true"><span class="exp">索书号:</span> <span v-html="data.lc_call_numbers"></span></p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="errorInfo1" v-if="errorInfo">
            <el-alert
                    title="错误提示"
                    type="error"
                    description="sorry，您访问的页面已走失"
                    show-icon>
            </el-alert>
        </div>
    </div>
</template>
<script type="text/javascript" src="../assets/js/jquery-1.12.4.min.js"></script>
<script>
    import {commonData} from '../assets/js/commonData.js'

    export default {

        data() {
            return {
                errorInfo:false,
                datas: [{}],
                flag: true,
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                $("a").removeAttr('href');
                this.tableView();
            })
        },
        methods: {

            tableView: function () {
                var val = commonData.searchContent;
//                this.datas = commonData.library(val);
//                $('.showDiv').css('display','none')
                var result = commonData.library(val);
                if (result instanceof Array) {
                    this.datas = result;
                    $('.showDiv').css('display','none')
                } else {
                    this.errorInfo = true;
                    $('.third-right').css('display','none');
                    $('.third-left').css('display','none');
                }
            },
            moreInfo:function(thi){
                var _this = this;
                if (_this.flag) {
                    console.log(thi.currentTarget);
                    $(thi.currentTarget).siblings($('.showDiv')).css('display','block');
                    $(thi.currentTarget).css('margin-top', '-10px!important');
                    _this.flag = false;
                } else {
                    $(thi.currentTarget).siblings($('.showDiv')).css('display','none');
                    _this.flag = true;
                }
            }
        }
    }
</script>

<style>
    html {
        word-break: break-all;
    }

    .thirdAll {
        clear: both;
    }

    .thirdAllContent2 {
        clear: both;
    }

    .title {
        color: cornflowerblue;
        /*font-size: 16px;*/
    }

    .author {
        text-decoration: underline;
        color: cornflowerblue;
        font-size: 14px;
    }

    .third-top p {
        font-size: 15px;
    }

    .third-left {
        width: 15%;
        float: left;
    }

    .third-left img {
        width: 50px;
        height: 70px;
        border-radius: 0;
    }

    .third-right {
        float: right;
        width: 82%;
        margin-top: 10px;
        /*font-size: 16px !important;*/
    }

    .third-top p {
        font-size: 15px !important;
    }

    .article {
        color: #666;
        text-decoration: underline;
    }

    .qikan {
        font-size: 15px !important;
    }

    .moreInfo {
        margin-top: 20px;
        color: cornflowerblue;
        text-decoration: underline;
    }

    .showDiv {
        display: none;
        /*margin-top: 10px;*/
        padding: 1px 0 5px 10px;
        border-left: 2px solid lightgray;
    }

    .doi {
        color: cornflowerblue !important;
        display: inline !important;
        font-weight: normal !important;
        font-size: 14px !important;

    }

    .showDiv p .exp {
        display: inline-block;
        /*width: 75px;*/
        text-align: left;
        font-weight: bold;
        font-size: 15px;
    }

    .third-foot {
        margin-top: -10px;
    }

    .showDiv p {
        font-size: 14px !important;
    }
</style>


