<template>
    <div id="third1" class="all">
        <div class="thirdAllContent2">

            <div class="thirdAll1">
                <el-table
                        v-if="tableShow"
                        :data="datas"
                        style="width: 100%">
                    <el-table-column type="expand">
                        <template scope="props">
                            <el-form label-position="left" inline class="demo-table-expand">
                                <el-form-item label="责任者">
                                        <span v-html="props.row.author" v-if="!props.row.author?false:true"></span>
                                </el-form-item>
                                <el-form-item label="出版信息">
                                        <span v-html="props.row.info" v-if="!props.row.info?false:true"></span>
                                </el-form-item>
                                <el-form-item label="索书号">
                                        <span v-html="props.row.number" v-if="!props.row.number?false:true"></span>
                                </el-form-item>
                                <el-form-item label="题名">
                                        <span v-html="props.row.title" v-if="!props.row.title?false:true"></span>
                                </el-form-item>
                                <el-form-item label="文献类型">
                                        <span v-html="props.row.type" v-if="!props.row.type?false:true"></span>
                                </el-form-item>
                            </el-form>
                        </template>
                    </el-table-column>
                    <el-table-column label="责任者">
                        <template scope="scope" >
                             <span v-html="scope.row.author"></span>
                        </template>
                    </el-table-column>
                    <el-table-column label="提名">
                        <template scope="scope" >
                            <span v-html="scope.row.title"></span>
                        </template>
                    </el-table-column>
                    <el-table-column label="文献类型">
                        <template scope="scope" >
                            <span v-html="scope.row.type"></span>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="errorInfo1" v-if="errorInfo">
                    <el-alert
                            title="错误提示"
                            type="error"
                            description="sorry，您访问的页面已走失"
                            show-icon>
                    </el-alert>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import {commonData} from '../assets/js/commonData.js'
    export default {
        data() {
            return {
                tableShow:true,
                errorInfo:false,
                datas: [{
                }]
            }
        },
        mounted: function () {
            this.$nextTick(function () {
                this.tableView()
            })
        },
        methods: {
            tableView: function () {
//                this.$router.go(0);
                var val = commonData.searchContent;
                var result = commonData.book(val);
                if (result instanceof Array) {
                    this.datas = result;
                    this.datas.shift();
                } else {
                    this.tableShow = false;
                    this.errorInfo = true;
                }
            },
        }
    }
</script>

<style>
    .thirdAll1 {
        position: relative;
        /*background: red;*/
        z-index: 99;
        clear: both;
    }

    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;

    }

    .el-table__expand-icon {
        line-height: 40px;
    }

    .el-icon {
        display: block;
        text-align: center;
        line-height: 45px;
        width: 16px;
        height: 16px;
        background-size: 16px 16px;
        background-image: url("../assets/imgs/arrow-right.png");
    }
</style>
export { Scholar };
<!--export { Router };-->


